
@font-face{

  font-family: 'Hansief';

  src: url(../../../Fonts/Hansief\ Rough.otf);
}

@font-face{

  font-family: 'Henrik-Regular';

  src: url(../../../Fonts/Henrik-Regular.otf);
}

@font-face{

  font-family: 'Jaime-Blues';

  src: url(../../../Fonts/Jaime\ Blues.ttf);
}

@font-face{

  font-family: 'OldGrowth-Regular';

  src: url(../../../Fonts/OldGrowth-Regular.otf);
}

@font-face{

  font-family: 'Hamurz';

  src: url(../../../Fonts/Hamurz\ Free\ Version.ttf);
}

.Credits--Display{

  display: flex;

  justify-content: space-between;

  width: 100%;

  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: normal;

}

.Credits--Section{

 position: relative;

  float: left;

  table-layout: fixed;

 line-height: normal;

 width: 100%;

 background-color: black;

 border-radius: 0px 0px 0px 20px;

 color: #fc5602;

}

.Credits--Authors-Title, .Credits--Source-Title {

  font-size: 30pt;
  font-family: 'Henrik-Regular';

  border-style: double;
  border-color: #fc5602;
  border-width: 0px 0px 10px 0px;
/*
    padding-top: 25pt;
    padding-bottom: 15pt;
*/
}

.Credits--Programmers-Title, .Credits--Researchers-Title {


  margin-top: 20px;

  font-size: 24pt;
  font-family: 'Hansief';
}

.Credits--Border-bar {
  margin: 5px;

  width: 100%;
  height: 3pt;

  background-color: #fc5602;
}


/*
 //////////////////// Samuel Card /////////////////////
*/

#Samuel--Card {

  color: #4ce300;
  position: relative;

  width: 100%;

  float: right;

  padding-left: 15px;

  padding-bottom: 35px;

  border-radius: 20px 0px 0px 20px ;

  box-shadow: 0px 0px 30px #4ce300;

/*>background-image: linear-gradient(to left, #4ce300, #740ecd);*/

  background-image: linear-gradient(to left, #4ce300 , black 60%);

  margin-top: 25px;
  margin-bottom: 25px;
}

#Samuel--Card img {

  float: right;

  /*
  border-style: solid;
  border-color: blueviolet;
  border-width: 3px;
  border-radius: 100%;
  */

  margin-right: -40px;

  max-width: 68%;
  max-height: 68%;

  border-radius: 10%;

  box-shadow: 0px 0px 10px #4ce300;
  padding-bottom: 0px;
}

#Samuel--Card .Link-Button {

  width: 40px;
  height: 40px;

  display:inline-block;

  margin-right: 18px;
}

#Samuel--Card .Link-Button:hover .Link-Button--border-bar{

  margin-top: -10px;
  margin-left: 3px;

  width: 85%;
  height: 2pt;

  border-radius: 25px;

  background-color: #4ce300;

}

#Samuel--Card .Card-contact {


}

