
#Content--Display{

  display: flex;

  justify-content: space-between;

  width: 100%;

  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: normal;


}

.Content--Section{

  position: relative;

  float: left;

  table-layout: fixed;


 line-height: normal;


}

.Menu {

  height: auto;

}

.content-summary--area .menu--section{

  border-radius: 10px;

  padding-left: 10px;
  margin-right: 25px;

  border-style: solid;
  border-width: 10px 0 10px 0;
  border-color: #08da94;
}

.content-summary--area .Link-Button a {

  color: #0c0074;
}



#Nervous--System {



  margin-right: 25px;
  overflow-wrap: break-word;
}

#Nervous--System article{

  padding-bottom: 50px;
}


#Nervous--System h1 {

  font-size: 28pt;
}

#Nervous--System h2 {

  font-size: 22pt;
}

#Nervous--System h3 {

  font-size: 18pt;
}


#Nervous--System p {
  text-align: justify;

  padding-left: 20pt;
}

#Nervous--System #intro{

  border-style: solid;
  border-width: 0px 0px 0px 8px;
  border-color: #0c0074;

  border-radius: 0px 10px 10px 0px;

  background-color: rgba(12, 0, 117, 0.3);

  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5pt;
  padding-bottom: 5pt;
}

#Nervous--System .list{

  border-style: solid;
  border-width: 0px 0px 8px 8px;
  border-color: #0c0074;

  border-radius: 5px 3px 0px 10px;

  background-color: rgba(12, 0, 117, 0.3);

  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5pt;
}

#Nervous--System #glia--section {

  border-style: solid;
  border-width: 0px 0px 0px 8px;
  border-color: #0c0074;

  border-radius: 5px 8px 8px 5px;

  background-color: rgba(12, 0, 117, 0.3);

  padding-left: 30pt;
  padding-top: 10pt;
  padding-bottom: 10pt;
  padding-right: 15pt;
}

#Nervous--System .section {

  border-style: solid;
  border-width: 0px 0px 0px 8px;
  border-color: #0c0074;

  border-radius: 5px 8px 8px 5px;

  background-color: rgba(12, 0, 117, 0.3);

  padding-left: 30pt;
  padding-top: 10pt;
  padding-bottom: 10pt;
  padding-right: 15pt;
}

#Nervous--System a {

  text-decoration: underline;
  color: black;
}


#Nervous--System li {

  padding-bottom: 10px;
}

